import * as React from "react"
import { Form, Button } from "react-bootstrap"
import "./style.css"
import Semester from "./Semester"

function SemesterList(props) {
  const { semesters, semester, setSemester, subArr, setSubArr, JSONsubjects } =
    props

  function addToArr(props) {
    subArr.push(props)
    setSubArr([...subArr])
    return subArr
  }

  function handleClickAddCourseToSubjectArr(course, grade, sem) {
    addToArr(course)
  }

  return (
    <div>
      <br />
      <h5>Choose Semester</h5>
      <Form.Select
        aria-label="Default select example"
        value={semester}
        onChange={e => {
          setSemester(e.target.value)
        }}
      >
        {semesters.map(sem => (
          <option key={sem} value={sem}>
            {sem}
          </option>
        ))}
      </Form.Select>
      <br />
      <Button variant="primary">New Semester</Button>
      <br />
      <br />
      <Semester
        semester={semester}
        JSONsubjects={JSONsubjects}
        handleClickAddCourseToSubjectArr={handleClickAddCourseToSubjectArr}
      />
    </div>
  )
}

export default SemesterList

